.animate-leave {
  opacity: 1;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  position: absolute !important;
  width: 100%;
}
.animate-leave.animate-leave-active {
  will-change: opacity, transform;
  -webkit-transition: opacity 0.3s ease-out, -webkit-transform 0.5s ease-out !important;
  transition: opacity 0.3s ease-out, -webkit-transform 0.5s ease-out !important;
  -o-transition: opacity 0.3s ease-out, transform 0.5s ease-out !important;
  transition: opacity 0.3s ease-out, transform 0.5s ease-out !important;
  transition: opacity 0.3s ease-out, transform 0.5s ease-out, -webkit-transform 0.5s ease-out !important;
  opacity: 0.01;
  -webkit-transform: translate3d(20px, 0, 0);
          transform: translate3d(20px, 0, 0);
}



